/**
 * Event Center
 */

export default class EventUtil{
    public static eventCenter = new cc.EventTarget();

    public static dispatchEvent(eventName : string, ...param){
        EventUtil.eventCenter.emit(eventName, param);
    }

    public static addEvent(eventName : string, callback:(event : cc.Event.EventCustom)=>void, thisObj:any):void{
        EventUtil.eventCenter.on(eventName,callback,thisObj);
    }

    public static removeEvent(eventName : string, callback:(event : cc.Event.EventCustom) => void, thisObj:any):void{
        EventUtil.eventCenter.off(eventName,callback,thisObj);
    }
}